<template>
  <div>
    <van-row type="flex" justify="space-between" align="center" class="top">
      <van-col span="6" class="l">
        <van-icon @click="back" name="arrow-left" />
      </van-col>
      <van-col span="8" class="c"></van-col>

      <van-col span="6" class="r">
        <van-icon @click.stop="show" name="qr" v-if="show"></van-icon>
      </van-col>
      <van-grid class="dao" direction="vertical" :column-num="2" v-show="showdao">
        <van-grid-item
          v-for="item in menu"
          :icon="item.icon"
          :key="item.text"
          :to="item.path"
          :text="item.text"
        ></van-grid-item>
      </van-grid>
    </van-row>
    <!--  -->
    <van-image :src="data.img_url" @click="showBig"></van-image>
    <div class="goods-info">
      <h1>{{data.goods_name}}</h1>
      <van-tag plain type="primary">{{data.category}}}</van-tag>
      <p class="price">
        <del>{{data.price}}</del>
        <span>{{data.sales_price}}</span>
      </p>
    </div>
    <h4></h4>
    <!--  -->
    <van-grid :border="false" :column-num="2" class="goodslist">
      <van-grid-item v-for="item in recommend" :key="item._id" @click="gotoDetail(item._id)">
        <van-image :src="item.img_url"></van-image>
        <h4>{{item.goods_name}}</h4>
        <p class="price">
          <del>{{item.price}}</del>
          <span>{{item.sales_price}}</span>
        </p>
      </van-grid-item>
    </van-grid>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="" color="#07c160" />
      <van-goods-action-icon
        icon="cart-o"
        text=""
        :badge="cartlist.length"
        @click="goto('/cart')"
      />

      <van-goods-action-icon icon="star" text="" color="#ff5000" />
      <van-goods-action-button type="warning" @click="add2cart" text="" />
      <van-goods-action-button type="danger" @click="buyNow" text="" />
    </van-goods-action>
  </div>
</template>
<script>
import Vue from "vue";
import {
  Icon,
  Row,
  Col,
  GoodsAction,
  GoodsActionButton,
  GoodsActionIcon,
  ImagePreview,
} from "vant";
Vue.use(GoodsAction);
Vue.use(GoodsActionButton);
Vue.use(GoodsActionIcon);
Vue.use(Col);
Vue.use(Row);
Vue.use(Icon);

// ImagePreview
export default {
  name: "Goods",
  data() {
    return {
      data: {},
      recommend: [],
    };
  },
  watch: {
    // '$route':function(to,from){
    //   const {id} = to.params;
    //   const {id:oldId} = from.params;
    //   if(id !== oldId){
    //     this.getData(id);
    //     this.getRecommend()
    //   }
    // }
  },
  computed: {
    cartlist() {
      //
      return this.$store.state.cart.goodslist;
    },
  },
  methods: {
    back() {
      window.history.go(-1);
    },
    show() {
      this.showdao = true;
      console.log(555);
    },
    hide() {
      this.showdao = false;
      console.log(66);
    },
    goto(path) {
      this.$router.push(path);
    },
    gotoDetail(id) {
      // this.$router.push(`/goods/${id}`)
      // this.$router.push({
      //   name:'Goods',
      //   params:{
      //     id
      //   }
      // })
      this.goto({
        name: "Goods",
        params: {
          id,
        },
      });
    },
    showBig() {
      ImagePreview({
        //
        images: [
          this.data.img_url,
          // "/img/740ff1324d84482ebaf35947059764f6_big.jpg",
          // "/img/2ce8ca193b8f43c799416cbafc7cb2f4_big.jpg",
          // "/img/21fc681cec49425797abc4e1167f5352_big.jpg",
        ],
        //closeableclose-iconclose-icon-position
        closeable: true,
      });
    },
    //
    async getData(id) {
      //axios
      // this.goodsRequestSource = this.$request.source;
      const { data } = await this.$request.get("/goods/" + id, {
        // cancelToken:this.goodsRequestSource.token
      });
      this.data = data.data;
      console.log("", this.data);
    },
    //
    async getRecommend() {
      const { data: recommend } = await this.$request.get("/goods/", {
        params: {
          sort: "sales_qty",
          total: 0,
        },
      });
      this.recommend = recommend.data;
      console.log("", this.recommend);
    },
    //
    async add2cart() {
      console.log(756);
      //
      //==+1
      //
      const { _id } = this.data;
      //
      const current = this.cartlist.filter((item) => item._id === _id)[0];
      if (current) {
        this.$store.commit("changeQty", { _id, qty: current.qty + 1 });
      } else {
        //
        const goods = {
          ...this.data,
          qty: 1,
        };
        console.log("", goods);
        this.$store.commit("add", goods);
        //shopcart
        const data = await this.$request.put("/insertGoods/", {
          params: {
            goods,
          },
        });
        console.log(data);
      }
    },
    buyNow() {
      //
      this.add2cart();
      this.$router.push("/cart");
    },
  },
  async created() {
    //id
    const { id } = this.$route.params;
    //
    this.getData(id);
    //
    this.getRecommend();
  },
  mounted() {
    //==>
    // this.$parent.showMenu = false;
    this.$store.commit("displayTabbar", false);
  },
  destroyed() {
    // this.$parent.showMenu = true;
    this.$store.commit("displayTabbar", true);
  },
  //
  beforeRouteUpdate(to, from, next) {
    if (to.params.id !== from.params.id) {
      this.getData(to.params.id);
      this.getRecommend();
    }
    next();
  },
};
</script>
<style lang="scss" scope>
.van-image {
  display: flex;
}
.van-image__img {
  margin: auto;
}

.goods-info {
  padding: 15px;
  h1 {
    font-size: 18px;
  }
}
.goodslist {
  h4 {
    font-size: 14px;
  }
}
.top {
  position: relative;
  height: 58px;
  background-color: #000;
  background-image: -webkit-linear-gradient(#323232, #414141);
  .l,
  .c,
  .r {
    font-size: 25px !important;
    font-weight: 400px;
    color: white;
  }
  .c {
    text-align: center;
    // background-color: green;
    // line-height: 31px;
  }
  .l {
    //     padding-left: 5px;
    //     //   background-color: yellow;
    //     padding-top: 4px;
    text-align: center;
  }
  .r {
    //     padding-left: 65px;
    //     //   background-color: red;
    //     padding-top: 4px;
    //     position: relative;
    text-align: center;
  }
  .dao {
    position: absolute;
    width: 180px;
    height: 200px;
    right: 0px;
    top: 58px;
    z-index: 9999;
    // background: red;
    .van-grid-item__content {
      background: #000;
      color: white;
      background-image: -webkit-linear-gradient(#323232, #414141);
      .van-grid-item__text {
        color: white;
      }
    }
  }
}
</style>